import { connect } from 'react-redux';
import {
  fetchingSelector,
  productPositionAssetsSelector
} from '../../redux/selectors/productPositionSelectors';
import BalancesListComponent from './BalancesListComponent';

const mapStateToProps = state => {
  return {
    fetching: fetchingSelector(state),
    balances: productPositionAssetsSelector(state)
  };
};

export default connect(mapStateToProps)(BalancesListComponent);



