import React from 'react';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import ProductIconContainer from '../common/ProductIcon/ProductIconContainer';

import './BalanceItem.css';

const balanceItemClasses = getBEMClasses('balance-item');

const BalanceItemComponent = props => {
  return (
    <div className={balanceItemClasses('container')}>
      <div className={balanceItemClasses('group')}>
        <div className={balanceItemClasses('icon')}>
          <ProductIconContainer
            iconFileName={props.balance.iconFileName}
            size={24}
          />
        </div>
        <div className={balanceItemClasses('product')}>
          {props.balance.Product + ' ' + props.balance.ProductFullName}
        </div>
      </div>
      <div className={balanceItemClasses('group')}>
        <div className={balanceItemClasses('position')}>
          {props.balance.Amount}
        </div>
      </div>
    </div>
  );
};

export default BalanceItemComponent;



