import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import BalancesItem from './BalanceItemComponent';
import Spinner from '../common/Spinner/Spinner';

import './BalancesListComponent.css';

const productBalancesClasses = getBEMClasses('product-balances');

const BalancesListComponent = (props, context) => {
  const items = props.balances.map(item => {
    return <BalancesItem key={item.ProductId} balance={item} />;
  });

  return (
    <div className={productBalancesClasses('list')}>
      <div className={productBalancesClasses('header')}>
        {context.t('Balances')}
      </div>
      {props.fetching ? (
        <Spinner />
      ) : (
        <div className={productBalancesClasses('container')}>{items}</div>
      )}
    </div>
  );
};

BalancesListComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default BalancesListComponent;



